import React from 'react';
import Movie from './Products/Movie'
import {Grid} from '@material-ui/core'
import Alita from '../resources/alita.jpg'
import Dragon from '../resources/dragon.jpg'
import Tu_nombre from '../resources/tu-nombre.jpg'
import Mascotas from '../resources/mascotas.jpg'
import Mowgli from '../resources/mowgli.jpg'
import Chijiro from '../resources/chijiro.jpg'
import Mariposa from '../resources/mariposa.jpg'
import Dioses from '../resources/dioses.jpg'
import Joker from '../resources/joker.jpg'
import Matrix from '../resources/matrix.jpg'
import './Products/Products.css'


const Movies = () => (
        
        <Grid container spacing={4} className='o-container'>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Mariposa} title={"El efecto mariposa"} duration={"1h 52min"} size={"1.5 Gb"} quality={"HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Alita} title={"Alita"} duration={"2h 2min"} size={"1.5 Gb"} quality={"HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Matrix} title={"Matrix"} duration={"2h 52min"} size={"1.3 Gb"} quality={"HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Dragon} title={"Cómo entrenar a tu dragón 3"} duration={"1h 44min"} size={"3.2 Gb"} quality={"Full HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Tu_nombre} title={"Your name"} duration={"1h 52min"} size={"2.5 Gb"} quality={"Full HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Mascotas} title={"La vida secreta de tus mascotas"} duration={"1h 40min"} size={"1.2 Gb"} quality={"HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Dioses} title={"Dioses de egipto"} duration={"2h 7min"} size={"1.7 Gb"} quality={"HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Chijiro} title={"El viaje del Chihiro"} duration={"1h 44min"} size={"1 Gb"} quality={"HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Mowgli} title={"Mowgli"} duration={"2h 5min"} size={"1.8 Gb"} quality={"Full HD"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Movie img={Joker} title={"Joker"} duration={"2h 2min"} size={"2 Gb"} quality={"Full HD"}/>
            </Grid>
            
        </Grid>    
);

export default Movies;