import React from 'react';
import {Grid} from '@material-ui/core'

import Logo from './HeaderSrc/Banner'

import LogoImg from '../resources/Header/logo.png'
import ImageImg from '../resources/Header/productos.png'



const Header = () => (
        
    <Grid container>
        <Grid item>
            <Logo imgLogo={LogoImg} title={"¡Bienvenidos!"} text={"En Multiproductos Digitales encontrarás tu productos digitales preferidos."}
                imgImage={ImageImg}/>
        </Grid>
    </Grid>    
);

export default Header;