import React from 'react';
import Program from './Products/Program'
import {Grid} from '@material-ui/core'

import Code from '../resources/Programs/code.jpg'
import Android from '../resources/Programs/android.jpg'
import Unreal from '../resources/Programs/unreal.jpg'
import Unity from '../resources/Programs/unity.jpg'
import Xd from '../resources/Programs/xd.jpg'
import Blender from '../resources/Programs/blender.jpg'
import Gimp from '../resources/Programs/gimp.jpg'
import Xampp from '../resources/Programs/xampp.jpg'
import Arduino from '../resources/Programs/arduino.jpg'
import Git from '../resources/Programs/git.jpg'


import './Products/Products.css'


const Programs = () => (
        
        <Grid container spacing={4} className='o-container'>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Code} title={"Visual Studio Code"} version={"1.44.2"} size={"57 Mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Android} title={"Android Studio"} version={"3.6.3"} size={"756 Mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Unreal} title={"unreal Engine"} version={"2019.3.9"} size={"4 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Unity} title={"Unity"} version={"1.44.2"} size={"57 Mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Xd} title={"Xd"} version={"28"} size={"1.5 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Blender} title={"Blender"} version={"2.82.a"} size={"129 Mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Gimp} title={"Gimp"} version={"2.10.12"} size={"224 Mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Xampp} title={"Xampp"} version={"7.4.5"} size={"148 Mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Arduino} title={"Arduino"} version={"1.8.12"} size={"112 Mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Program img={Git} title={"Git"} version={"2.26.2"} size={"45 Mb"}/>
            </Grid>
        </Grid>    
);

export default Programs;