import React from 'react';
import Song from './Products/Song'
import {Grid} from '@material-ui/core'
import Purpura from '../resources/Songs/purpura.jpg'
import Tengo from '../resources/Songs/tengo.jpg'
import Guapa from '../resources/Songs/guapa.jpg'
import Pecador from '../resources/Songs/pecador.jpg'
import Sentimiento from '../resources/Songs/sentimiento.jpg'
import Mejor from '../resources/Songs/mejor.jpg'
import Nuqui from '../resources/Songs/nuqui.jpg'
import Rimas from '../resources/Songs/rimas.jpg'
import Mocca from '../resources/Songs/mocca.jpg'
import Siempre from '../resources/Songs/siempre.jpg'


import './Products/Products.css'


const Songs = () => (
        
        <Grid container spacing={4} className='o-container'>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Purpura} title={"Púrpura"} author={"Nanpa Básico"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Tengo} title={"Si no te tengo"} author={"Green Valley"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Guapa} title={"Guapa"} author={"El Chojin"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Pecador} title={"Pecador"} author={"Residente"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Sentimiento} title={"Sentimiento original"} author={"Gondwana"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Mejor} title={"Algo Mejor"} author={"Zona Ganjah"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Nuqui} title={"Nuqui"} author={"Chocquibtown"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Rimas} title={"Rimas pa' seducir"} author={"Cultura Profetica"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Mocca} title={"Mocca"} author={"Lalo"} />
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Song img={Siempre} title={"Para siempre"} author={"kany Garcia"} />
            </Grid>
        </Grid>    
);

export default Songs;