import React from 'react';
import './Products.css'

const Movie = ({img, title, author}) => {
    return(
        <div className='o-movie'>
            <img className='o-img' src = {img}/>
            <div className='o-infoS'>
                <h3 className='o-title'>{title}</h3>
                <h4 className='o-h4'>{author}</h4>
            </div>
        </div>
    );
};

export default Movie;