import React from "react";
import "./Header.css";
import { Grid } from "@material-ui/core";

const Logo = ({ imgLogo, title, text, imgImage }) => {
    return (
        <div className="o-logo">
            <Grid container centered>
                <Grid item lg={4} md={4} sm={4} xs={6} className="o-containerImgLogo">
                    <img className="o-imgLogo" src={imgLogo} />
                </Grid>
                <Grid item lg={4} md={4} sm={4} xs={6} className="o-containerText">
                    <div className="o-text">
                        <h1>{title}</h1>
                        <p>{text}</p>
                    </div>
                </Grid>
                <Grid item lg={4} md={4} sm={4} xs={6} className="o-containerImg">
                    <img className="o-imgImage" src={imgImage} />
                </Grid>
            </Grid>
        </div>
    );
};

export default Logo;
