import React from 'react';
import Game from './Products/Game'

import {Grid} from '@material-ui/core'
import Crash from '../resources/Games/crash.jpg'
import Minecraft from '../resources/Games/minecraft.jpg'
import Risen from '../resources/Games/risen.jpg'
import Sims from '../resources/Games/sims.jpg'
import San from '../resources/Games/san.jpg'
import Gta from '../resources/Games/gta.jpg'
import Nfs from '../resources/Games/nfs.jpg'
import Mario from '../resources/Games/mario.jpg'
import Halo from '../resources/Games/halo.jpg'
import Cause from '../resources/Games/cause.jpg'





import './Products/Products.css'


const Games = () => (
        
        <Grid container spacing={4} className='o-container'>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Crash} title={"Crash bandicoot"} size={"4.43 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Minecraft} title={"Minecraft"} size={"200mb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Risen} title={"Risen 2"} size={"6.27 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Sims} title={"The sims 4"} size={"15.5 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={San} title={"San Andreas"} size={"3.34 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Gta} title={"Gta v"} size={"59 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Nfs} title={"NFS Underground 2"} size={"1.32 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Mario} title={"Mario Kart 8"} size={"6.75 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Halo} title={"Halo 2"} size={"3.53 Gb"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Game img={Cause} title={"Just Cause 2"} size={"4 Gb"}/>
            </Grid>
        </Grid>    
);

export default Games;