import React from 'react';
import Book from './Products/Book'
import {Grid} from '@material-ui/core'
import Ilusiones from '../resources/Books/ilusiones.jpg'
import Dracula from '../resources/Books/dracula.jpg'
import Aztecas from '../resources/Books/aztecas.jpg'
import Dhammapada from '../resources/Books/dhammapada.jpg'
import Emocional from '../resources/Books/emocional.jpg'
import Sentido from '../resources/Books/sentido.jpg'
import Shiddhartha from '../resources/Books/siddhartha.jpg'
import Isla from '../resources/Books/isla.jpg'
import Funerales from '../resources/Books/funerales.jpg'
import Putas from '../resources/Books/putas.jpg'


import './Products/Products.css'


const Books = () => (
        
        <Grid container spacing={4} className='o-container'>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Ilusiones} title={"Ilusiones"} author={"Richard Bach"} pages={"144 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Dracula} title={"Dracula"} author={"Bram Stoker"} pages={"325 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Aztecas} title={"Aztecas"} author={"Victor W"} pages={"120 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Dhammapada} title={"Dhammapada"} author={"Enseñanzas de buda"} pages={"400 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Emocional} title={"inteligencia Emocional"} author={"Daniel Goleman"} pages={"350 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Sentido} title={"El hombre en busca de sentido"} author={"Victor Frank"} pages={"150 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Shiddhartha} title={"Shiddhartha"} author={"Hermann Hesse"} pages={"174 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Isla} title={"San Borondón"} author={"Pedro Gonzales"} pages={"170 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Funerales} title={"Los funerales de mamá grande"} author={"Gabriel Garcia Marquez"} pages={"115 páginas"}/>
            </Grid>
            <Grid item lg={3} md={4} sm={6} xs={12}>
                <Book img={Putas} title={"Memoria de mis putas tristes"} author={"Gabriel Garcia Marquez"} pages={"150 páginas"}/>
            </Grid>
            
        </Grid>    
);

export default Books;