import React from 'react';
import './Products.css'

const Movie = ({img, title, version, size}) => {
    return(
        <div className='o-movie'>
            <img className='o-img' src = {img}/>
            <div className='o-infoP'>
                <h3 className='o-title'>{title}</h3>
                <h4 className='o-h4'>{version}</h4>
                <h4 className='o-h4'>{size}</h4>
               
            </div>
        </div>
    );
};

export default Movie;