import React from "react";

import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import {Button} from '@material-ui/core'

import { black } from "@material-ui/core/colors";

import MovieIcon from '@material-ui/icons/Movie'
import SportsEsportsIcon from '@material-ui/icons/SportsEsports';
import LibraryMusicIcon from '@material-ui/icons/LibraryMusic';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import DesktopMacIcon from '@material-ui/icons/DesktopMac';



import Movies from './Movies'
import Games from './Games'
import Songs from './Songs'
import Books from './Books'
import Programs from './Programs'



  function TabPanel(props) {
    const { children, value, index, ...other } = props;
  
    return (
      <Typography
        component="div"
        role="tabpanel"
        hidden={value !== index}
        id={`simple-tabpanel-${index}`}
        aria-labelledby={`simple-tab-${index}`}
        {...other}
      >
        {value === index && <Box p={3}>{children}</Box>}
      </Typography>
    );
  }
  
  TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
  };
  
  function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }
  
  const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      backgroundColor: theme.palette.background.paper,
    },

    menu: {
      backgroundColor: '#212121',
  },
  }));
  
  export default function SimpleTabs() {
    const classes = useStyles();
    const [value, setValue] = React.useState(0);
  
    const handleChange = (event, newValue) => {
      setValue(newValue);
    };

    

  return (
    <div className={classes.root}>
      <AppBar position="static" className={classes.menu}>
        <Tabs 

        value={value} onChange={handleChange} 
        
        centered
        
        >
          <Tab label="Películas" icon={<MovieIcon className={classes.icon}/>} {...a11yProps(0)} indicatorColor="primary"/>
          <Tab label="Videojuegos" icon={<SportsEsportsIcon/>} {...a11yProps(1)} />
          <Tab label="Canciones" icon={<LibraryMusicIcon/>} {...a11yProps(2)} />
          <Tab label="Libros" icon={<MenuBookIcon/>} {...a11yProps(3)} />
          {/* <Tab label="Programas" icon={<DesktopMacIcon/>} {...a11yProps(4)}/> */}
          
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0}>
        <Movies />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <Games/>
      </TabPanel>
      <TabPanel value={value} index={2}>
        <Songs/>
      </TabPanel>
      <TabPanel value={value} index={3}>
        <Books/>
      </TabPanel>
      <TabPanel value={value} index={4}>
        <Programs/>
      </TabPanel>
    </div>
    /*<Grid container>

      <Grid item md={6}>
        <AirlineSeatIndividualSuiteIcon color="primary"></AirlineSeatIndividualSuiteIcon>
      </Grid>

      <Grid item md={6}>
        <Button variant="contained" color="secondary" startIcon={<MovieIcon />}>
          Pelicula
        </Button>
      </Grid>

      <Grid item>
        <IconButton aria-label="movie">
          {/* <MovieIcon color='primary'/> }
          <MovieIcon style={{ color: green[500] }} />
        </IconButton>
      </Grid>

    </Grid>*/
  );
}

